import React, {Component, Fragment} from 'react';
import {Button, Col, Container, Row} from "react-bootstrap";
import "./ProjectDetails.css";
import RestClient from "../../RestAPI/RestClient";
import AppUrl from "../../RestAPI/AppUrl";
import ReactHtmlParser from "react-html-parser";
import Loading from "../Loading/Loading";
import WentWrong from "../WentWrong/WentWrong";

class ProjectDetails extends Component {

    constructor(props) {
        super();
        this.state={
            MyProjectID:props.id,
            img_two:"",
            project_name:"",
            project_description:"",
            project_features:"",
            live_preview:"",
            loading: true,
            wrong: false
        }
    }

    componentDidMount() {
        RestClient.GetRequest(AppUrl.ProjectDetails+this.state.MyProjectID).then(result=>{

            if(result == null){
                this.setState({wrong: true, loading: false})
            }

            else{
                this.setState({
                    img_two:result[0]['img_two'],
                    project_name:result[0]['project_name'],
                    project_description:result[0]['project_description'],
                    project_features:result[0]['project_features'],
                    live_preview:result[0]['live_preview'],
                    loading: false
                })            }



        }).catch(error=>{
            this.setState({wrong: true, loading: false})

        })
    }


    render() {

        if(this.state.loading == true && this.state.wrong == false){
            return <Loading/>
        }

        else if(this.state.loading == false && this.state.wrong == true){
            return <WentWrong/>
        }

        else {

        return (
            <Fragment>
                <Container className="mt-5">
                    <Row>
                        <Col lg={6} sm={12} md={6}>
                            <img className="projectImg" src={this.state.img_two}/>
                        </Col>
                        <Col lg={6} sm={12} md={6}>

                            <h1 className="serviceName">{this.state.project_name}</h1>
                            <p className="serviceDescription">{this.state.project_description}</p>
                            {ReactHtmlParser(this.state.project_features)}

                            <Button target="_blank" href={"//"+this.state.live_preview} variant="primary">More Info</Button>

                        </Col>
                    </Row>
                </Container>


            </Fragment>
        );
    }
    }
}

export default ProjectDetails;